:root {
    --bs-scrollbar: 0px;
}

.screen-width,
.screen-full {
    width: 100%;
}
@media screen {
    .screen-width,
.screen-full {
        width: 100vw;
        width: calc(100vw - (100vw - 100%));
    }
}

.screen-width,
.screen-height {
    height: 100%;
}
@media screen {
    .screen-width,
.screen-height {
        height: 100vh;
    }
}

.address-line .address-item {
    display: inline-block;
    margin-right: var(--bs-spacer-default);
}
.address-line .address-item.address-item-separator {
    margin-left: calc(-1 * var(--bs-spacer-default));
}

abbr[title].address-line {
    text-decoration: none;
}

:root {
    --bs-spacer-hair: 0.0417em;
    --bs-spacer-fine: 0.125em;
    --bs-spacer-one-sixth: 0.1667em;
    --bs-spacer-thin: 0.2em;
    --bs-spacer-math: 0.2222em;
    --bs-spacer-default: 0.25em;
    --bs-spacer-thick: 0.2778em;
    --bs-spacer-one-third: 0.3333em;
    --bs-spacer-en: 0.5em;
    --bs-spacer-em: 1em;
    --bs-spacer: 1rem;
    --bs-nav-icon-size: max-content;
    --bs-gutter-x: 1.5rem;
    --bs-gutter-y: 1.5rem;
    --bs-row-gutter-y: calc(var(--bs-gutter-y) * 2);
    --bs-section-gutter-y: calc(var(--bs-gutter-y) * 3);
    --bs-container-width: 90vw;
    --bs-container-width: 90vw;
    --bs-container-sm-width: 90vw;
    --bs-container-md-width: 85vw;
    --bs-container-lg-width: 80vw;
    --bs-container-xl-width: 75vw;
    --bs-container-xxl-width: 1050px;
}
@media (min-width: 576px) {
    :root {
        --bs-container-width: 90vw;
    }
}
@media (min-width: 768px) {
    :root {
        --bs-container-width: 85vw;
    }
}
@media (min-width: 992px) {
    :root {
        --bs-container-width: 80vw;
    }
}
@media (min-width: 1200px) {
    :root {
        --bs-container-width: 75vw;
    }
}
@media (min-width: 1400px) {
    :root {
        --bs-container-width: 1050px;
    }
}

body.rwp:not(.wp-admin) #wpadminbar {
    max-width: 100vw;
    overflow-x: hidden;
    display: flex;
}
body.rwp:not(.wp-admin) #wpadminbar .quicklinks {
    display: flex;
    justify-content: space-between;
}
body.rwp:not(.wp-admin) #wpadminbar .quicklinks > ul {
    display: flex;
}
body.rwp:not(.wp-admin) #wpadminbar .quicklinks #wp-admin-bar-top-secondary {
    margin-left: auto;
}
body.rwp:not(.wp-admin) #wpadminbar #wp-admin-bar-edit > .ab-item {
    position: absolute !important;
    width: 1px !important;
    height: 1px !important;
    padding: 0 !important;
    margin: -1px !important;
    overflow: hidden !important;
    clip: rect(0, 0, 0, 0) !important;
    white-space: nowrap !important;
    border: 0 !important;
}
@media screen and (max-width: 600px) {
    body.rwp:not(.wp-admin) #wp-admin-bar-my-account {
        height: 46px;
    }
}

.screen-reader-text {
    position: absolute !important;
    width: 1px !important;
    height: 1px !important;
    padding: 0 !important;
    margin: -1px !important;
    overflow: hidden !important;
    clip: rect(0, 0, 0, 0) !important;
    white-space: nowrap !important;
    border: 0 !important;
}

.entry-content img {
    max-width: 100%;
    height: auto;
}

.wp-block-image.is-style-rounded > div,
.wp-block-image.is-style-rounded > picture {
    --bs--aspect-ratio: 100%;
    position: relative;
    width: 100%;
}
.wp-block-image.is-style-rounded > div::before,
.wp-block-image.is-style-rounded > picture::before {
    display: block;
    padding-top: var(--bs-aspect-ratio);
    content: "";
}
.wp-block-image.is-style-rounded > div > *,
.wp-block-image.is-style-rounded > picture > * {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.wp-block-image.is-style-rounded > div img,
.wp-block-image.is-style-rounded > div svg,
.wp-block-image.is-style-rounded > picture img,
.wp-block-image.is-style-rounded > picture svg {
    min-width: 100%;
    min-height: 100%;
    width: 100%;
    height: 100%;
    display: block;
    object-fit: cover;
    font-family: "object-fit: cover; parent-fit: cover";
    max-width: none;
    border-radius: 0;
}

hr.wp-block-separator {
    border: none !important;
