body.rwp {
	&:not(.wp-admin) {
		#wpadminbar {
			max-width: 100vw;
			overflow-x: hidden;
			display: flex;

			.quicklinks {
				display: flex;
				justify-content: space-between;

				> ul {
					display: flex;
				}

				#wp-admin-bar-top-secondary {
					margin-left: auto;
				}
			}

			#wp-admin-bar-edit > .ab-item {
				@include visually-hidden;
			}
		}

		#wp-admin-bar-my-account {
			@media screen and (max-width: 600px) {
				height: 46px;
			}
		}
	}
}

.screen-reader-text {
	@include visually-hidden;
}

.entry-content img {
	@include img-fluid;
}
